@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;900&display=swap');

/* Animation Pulse */
.pulse:hover{ 
  animation: pulse .7s 1;
  animation-timing-function: linear;   
}
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
  }
  
* {
  box-sizing: border-box;
}

body {
  background: #ffd5c0;
  font-family: 'Lato', sans-serif;
  background-image:
  linear-gradient(#fff 1px, transparent 2px),
  linear-gradient(90deg, #fff 1px, transparent 2px);
  background-size: 150px 150px;
  text-transform: uppercase;
}

h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}

h1 {
  font-family: 'Work Sans', sans-serif;
  -webkit-text-stroke: 1px #ffb38d;
  font-weight: 900;
  margin-bottom: 0;
}

.app {
  padding: 30px;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-container {
  background: #ffb38d;
  border-radius: 10px;
  padding: 30px;
  width: 70%;
}

.ais-Stats {
  text-align: right;
  color: #fff;
  opacity: 0.8;
  margin-bottom: 15px;
}

.ais-SearchBox-form {
  width: 100%;
  display: flex;
  position: relative;
  margin-bottom: 30px;
}

.ais-SearchBox-form input[type='search'] {
  background: #ff7937;
  border-radius: 10px;
  padding: 20px 20px;
  border: none;
  outline: none;
  width: 100%;
  font-size: 28px;
  color: #eee;
}

.ais-SearchBox-submit,
.ais-SearchBox-reset {
  display: none;
}

.ais-Hits-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.ais-Hits-list {
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr 1fr;
}

@media (min-width: 780px) {
  .ais-Hits-list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.ais-Pagination-list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.ais-Pagination-item {
  flex: 1;
}

.ais-Pagination-link {
  display: block;
  padding: 15px 20px;
  background: #ff7937;
  color: #fff;
  text-decoration: none;
  text-align: center;
}

.ais-Pagination-link:hover {
  background: #ff5f0f;
  color: #eee;
}

::placeholder {
  color: #eee;
}

.product {
  background: #fff;
  border-radius: 20px;
  padding: 30px 30px;
  height: 100%;
  display: flex;
  text-decoration: none;
  color: #916b4e;
}

.product > div:not(:last-child) {
  margin-bottom: 8px;
}

.product img {
  max-width: 100px;
  margin-right: 20px;
  object-fit: contain;
}

.product h3 {
  font-size: 14px;
  opacity: 0.5;
  margin: 0 0 5px;
}

.product h2 {
  font-size: 18px;
  margin: 0 0 10px;
}

.product h2 span {
  font-size: 16px;
  opacity: 0.6;
  display: block;
}

.product p {
  font-size: 14px;
  margin: 0;
  opacity: 0.5;
}
